.header {
  background-image: url(../images/header.png);

  background-size: cover;
  background-position: center;
  margin-bottom: 120px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    min-height: 500px;
    padding: 40px 0 94px;

    @include onTablet {
      padding: 52px 0 40px;
    }

    @include onDesktop {
      padding: 52px 0 64px;
    }
  }

  &__buttons {
    display: flex;
  }

  &__phone {
    margin-right: 30px;
    text-decoration: none;

    @include onDesktop {
      &::before {
        display: block;
        width: max-content;
        padding-right: 12px;

        content: "+1 234 555-55-55";

        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        color: $mainColor;

        opacity: 0;
        pointer-events: none;
        transform: translate(-100%, 50%);
        transition: opacity 0.5s;
      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }

    @include onTablet {
      margin-right: 40px;
    }
  }

  &__title {
    font-style: italic;
    font-weight: 800;
    font-size: 32px;
    line-height: 100%;

    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 40px;
    }

    @include onDesktop {
      font-size: 48px;
    }

    &::before {
      display: block;
      content: "";
      height: 27px;
      width: 32px;
      margin-bottom: 16px;

      background-image: url(../images/sound_waves_mobile.svg);
      background-size: cover;

      @include onTablet {
        height: 76px;
        width: 63px;
        margin-bottom: 40px;
        background-image: url(../images/sound_waves.svg);
      }

      @include onDesktop {
        margin-bottom: 32px;
      }
    }
  }
}
